/* General styling for the home container */
.home {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  /* Header styling */
  header h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  header p {
    text-align: center;
    margin-bottom: 20px;
    color: #555;
  }
  
  /* Navigation styling */
  .nav-links {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: -60px;
    margin-right: -50px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #e6faffbd;
    font-weight: bold;
    padding: 8px 16px;
  }
  
  .nav-links a:hover {
    background-color: #6f6f6f6d;
    color: #ffffffaf;
    border-radius: 5px;
  }
  
  /* Form styling */
  form {
    display: flex;
    flex-direction: column;
  }
  
  form input, form textarea {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  form button {
    padding: 10px 20px;
    background-color: #8f7606b2;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  form button:hover {
    background-color: #0056b3;
  }
  
  /* Star rating styling */
  form .fa-star {
    font-size: 1.5rem;
    margin-right: 5px;
    transition: color 0.3s;
  }
  
  /* User comments section */
  .comments-section .comment {
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .comments-section .comment p {
    margin: 5px 0;
  }
  
  /* Footer styling */
  footer {
    text-align: center;
    margin-top: 30px;
    color: #777;
  }
  
  /* Responsive design */
  @media (max-width: 600px) {
    .nav-links {
      flex-direction: column;
      align-items: center;
    }
  
    .nav-links a {
      margin-bottom: 5px;
    }
  }
  .converter-info {
    max-width: 900px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
  }
  
  .converter-info h1 {
    color: #333;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .converter-info h3 {
    color: #555;
    margin-top: 15px;
    margin-bottom: 10px;
  }
  
  .converter-info p,
  .converter-info ol,
  .converter-info ul {
    color: #555;
    margin-bottom: 15px;
  }
  
  .converter-info ul {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  .converter-info ol {
    list-style-type: decimal;
    padding-left: 20px;
  }
  
  .converter-info li {
    margin-bottom: 8px;
  }
  
  .video-wrapper {
    text-align: center;
    margin-top: 20px;
  }
  
  .video-wrapper iframe {
    width: 100%;
    max-width: 560px;
    height: 315px;
    border-radius: 10px;
  }
  
  .converter-info .link {
    color: #007BFF;
    text-decoration: none;
    font-weight: bold;
  }
  
  .converter-info .link:hover {
    text-decoration: underline;
  }
  
  /* Responsive adjustments */
  @media (max-width: 600px) {
    .converter-info {
      padding: 15px;
    }
  
    .converter-info h1 {
      font-size: 1.5rem;
    }
  
    .converter-info h3 {
      font-size: 1.2rem;
    }
  
    .video-wrapper iframe {
      height: 250px;
    }
  }
  @media (max-width: 767px) {
    .slider-btn {
      padding: 8px; /* Smaller button padding on smaller screens */
    }
  
    .slider-image {
      max-width: 90%; /* Use a larger percentage of the screen width */
    }
  }
  
  @media (max-width: 540px) {
    .slider-btn {
      padding: 6px; /* Smaller button padding on very small screens */
      font-size: 1em;
    }
  
    .slider-image {
      max-width: 100%; /* Make the image take up the full width */
    }
  }
  