/* General Styles */
body {
  background-color: #f0f0f0;
  margin: 0;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  animation: backgroundAnimation 10s infinite alternate;
}

@keyframes backgroundAnimation {
  0% { background-color: #ffcccb; }
  50% { background-color: #add8e6; }
  100% { background-color: #90ee90; }
}

.app {
  text-align: center;
  padding: 20px;
}

header {
  background-color: #4a90e2;
  color: #fff;
  padding: 30px;
  animation: fadeIn 1s ease-in;
  margin-top: -50px;
  margin-right: -20px;
  margin-left: -20px;
}

footer {
  background-color: #333;
  color: #fff;
  padding: 15px;
  margin-top: 40px;
  text-align: center;
  position: relative;
  overflow: hidden;
  margin-bottom: -50px;
  margin-right: -20px;
  margin-left: -20px;
}

.footer-icons {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

.footer-icons a {
  color: #fff;
  margin: 0 10px;
  transition: transform 0.3s ease;
}

.footer-icons a:hover {
  transform: scale(1.2);
}

button {
  padding: 15px 20px;
  background-color: #ff5722;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
  background-color: #e64a19;
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

.format-descriptions {
  text-align: left;
  max-width: 600px;
  margin: auto;
  margin-top: 20px;
}

.format-descriptions div {
  margin: 15px 0;
  padding: 10px;
  border: 2px dashed #4a90e2;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.file-input-container {
  position: relative;
  display: inline-block;
}

.custom-file-upload {
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border: 2px solid #28a745;
  border-radius: 5px;
  background-color: #f8f9fa;
  color: #28a745;
  transition: background-color 0.3s, border-color 0.3s;
}

.custom-file-upload:hover {
  background-color: #28a745;
  color: white;
  border-color: #28a745;
}

.custom-file-input {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
}

.custom-file-input input[type="file"] {
  display: none;
}

.custom-file-input label {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.service-cards {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  flex-wrap: wrap; /* Allows wrapping on smaller screens */
}

.card {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  width: calc(33.33% - 20px); /* Adjusts the width for spacing */
  margin-bottom: 20px; /* Space between rows */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.card:hover {
  transform: scale(1.05);
}

/* Media Queries for Responsiveness */

/* Tablet and below */
@media (max-width: 767px) {
  body {
    font-size: 14px; /* Adjust font size for smaller devices */
  }

  header {
    padding: 20px; /* Reduce header padding on smaller screens */
    margin-left: 0;
    margin-right: 0;
  }

  footer {
    padding: 10px;
  }

  .format-descriptions {
    padding: 0 10px; /* Add padding to format descriptions */
  }

  .service-cards {
    justify-content: center; /* Center the service cards */
  }

  .card {
    width: calc(50% - 20px); /* Use 2 cards per row on smaller screens */
    margin-bottom: 15px;
  }

  button {
    padding: 12px 15px; /* Make buttons smaller */
  }

  .custom-file-upload, .custom-file-input label {
    padding: 8px 16px; /* Make file upload buttons smaller */
  }
}
/* Mobile and below */
@media (max-width: 540px) {
  body {
    font-size: 12px; /* Further reduce font size for mobile */
  }

  header {
    padding: 15px; /* Smaller padding in header */
  }

  footer {
    padding: 8px; /* Smaller footer padding */
  }

  .format-descriptions {
    padding: 0 5px; /* Add padding for mobile view */
  }
  .service-cards {
    display: flex;
    flex-wrap: wrap; /* Allow wrapping of cards */
    justify-content: space-between; /* Distribute cards evenly */
    gap: 10px; /* Space between the cards */
  }
 
  
 
  button {
    padding: 10px 12px; /* Smaller buttons for mobile */
  }

  .custom-file-upload, .custom-file-input label {
    padding: 6px 12px; /* Smaller file upload buttons */
  }
}
@media (max-width: 540px) {
  .card {
    width: 100%; /* Full width on smaller screens */
    max-width: 100%; /* Ensure no width limitations */
    margin: 10px auto; /* Center the card */
    
    /* Flexbox to display text horizontally */
    display: flex;
    flex-direction: column; /* Align text horizontally */
    align-items: center; /* Vertically center the text */
    justify-content: flex-start; /* Left-align the text */
    gap: 10px; /* Optional: Space between the title and description */
  }

}
