/* Social Media Sidebar */
.social-media-sidebar {
    position: fixed;
    top: 40%;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: #fff;
    padding: 10px;
    border-radius: 0 8px 8px 0;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
  }
  
  .social-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f1f1f1;
    transition: transform 0.2s ease;
    cursor: pointer;
  }
  
  .social-button:hover {
    transform: scale(1.1);
  }
  
  /* Main Content Styling */
  .main-content {
    padding-left: 60px; /* Provides space so content is not hidden by sidebar */
  }
  
  .content {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .h1 {
    font-size: 2em;
    color: #333;
  }
  
  .p {
    font-size: 1.2em;
    color: #555;
  }
  