.articles-page {
  max-width: 1000px;
  margin-top: 0px;
  margin-bottom: -40px;
  height: 3700px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 20px;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  line-height: 1.8;
  box-shadow: 0 0 15px #75257592;
  animation: pulse 3s infinite;
}
strong{
  text-transform: uppercase;
}
@keyframes pulse {
  0%, 100% {
      box-shadow: 0 0 15px #4c424c82;
  }
  50% {
      box-shadow: 0 0 30px #6454548e;
  }
}

.articles-page h1 {
  text-align: center;
  color: #46352f;
  margin-top: 50px;
  margin-bottom: 50px;
  text-transform: uppercase;
}

@keyframes rotate-title {
  0% {
      transform: rotate(0deg);
  }
  50% {
      transform: rotate(3deg);
  }
  100% {
      transform: rotate(-3deg);
  }
}

.articles-page p {
  color: #333;
  margin-bottom: 20px;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 10px;
}

.article-links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-items: center;
  margin-top: 50px;
}
.article:nth-child(2n) {
  margin-top: 30px;
}
.article:nth-child(1n) {
  margin-top: 30px;
}

.article {
  background-color: #a5a5a593;
  width: 400px;
  padding: 15px;
  border-radius: 15px;
  border: none;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.518);
  transform: scale(1);
  transition: transform 0.3s;
}

.article:hover {
  transform: scale(1.01);
  box-shadow: 0 5px 25px #7d7d7d;
}

.article h2 {
  color: #365448e6;
  margin-bottom: 10px;
  text-shadow: 1px 1px 2px #ffffff53;
}

.article p {
  color: #222;
}

.article-link {
  color: #008000;
  text-decoration: none;
  font-weight: bold;
  
}

.article-link:hover {
  color: #ff4500;
  text-decoration: underline;
}

/* Responsive adjustments */
@media (min-width:1200px) {
  .articles-page {
  margin-left: 100px;
  
}
}
@media (max-width: 800px) {
  .articles-page {
      padding: 15px;
  margin-left: 10px;
  height: 5050px;


  }

  .articles-page h1 {
      font-size: 1.8rem;
  }

  .article h2 {
      font-size: 1.2rem;
  }

  .article-links {
    grid-template-columns: 1fr;
  }
}
