/* Basic Styling */
body {
    font-family: Arial, sans-serif;
    background-color: #f7f7f7;
    color: #2c3e50;
    margin: 0;
    padding: 0;
  }
  
  .h1 {
    text-align: center;
    font-size: 2.4em;
    color: #2980b9;
    margin-top: 20px;
  }
  
  .contact-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px 30px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    line-height: 1.6;
  }
  
  .contact-details {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
  }
  
  .contact-info, .contact-form {
    flex: 1 1 300px;
  }
  
  .contact-info h2, .contact-form h2 {
    font-size: 1.6em;
    color: #34495e;
    margin-bottom: 0.5em;
  }
  
  .contact-info p {
    margin: 0.4em 0;
  }
  
  .contact-info a {
    color: #2980b9;
    text-decoration: none;
    transition: color 0.2s ease;
  }
  
  .contact-info a:hover {
    color: #1c6ea4;
  }
  
  /* Form Styling */
  .contact-form label {
    display: block;
    margin-top: 1em;
    font-weight: bold;
    color: #34495e;
  }
  
  .contact-form input, .contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-top: 0.5em;
    border: 1px solid #dcdcdc;
    border-radius: 4px;
    font-size: 1em;
    font-family: inherit;
  }
  
  .contact-form textarea {
    resize: vertical;
  }
  
  .contact-form button {
    margin-top: 1.5em;
    background-color: #2980b9;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-form button:hover {
    background-color: #1c6ea4;
  }
  
  /* Responsive Design */
  
  /* Tablets */
  @media (max-width: 767px) {
    .contact-container {
      padding: 15px;
    }
  
    h1 {
      font-size: 2em;
    }
  
    .contact-info, .contact-form {
      flex: 1 1 100%;
    }
  }
  
  /* Mobile Devices */
  @media (max-width: 540px) {
    h1 {
      font-size: 1.8em;
    }
  
    .contact-container {
      padding: 10px;
    }
  
    .contact-form label {
      font-size: 0.9em;
    }
  }
  