/* Basic Styling */
body {
    background-color: #f7f7f7;
    color: #2c3e50;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .h1, .h2 {
    color: #2c3e50;
    margin: 0;
    padding: 0.5em 0;
    font-weight: 600;
  }
  
  .h1 {
    font-size: 2.4em;
    text-align: center;
    color: #2980b9;
  }
  
  .h2 {
    font-size: 1.6em;
    margin-top: 1.5em;
    color: #34495e;
  }
  
  .terms-of-service {
    max-width: 850px;
    margin: 20px auto;
    padding: 20px 30px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    line-height: 1.7;
    color: #2c3e50;
  }
  
  .terms-of-service p {
    font-size: 1em;
    margin: 1em 0;
  }
  
  .terms-of-service ul {
    padding-left: 1.5em;
    list-style: square;
  }
  
  .terms-of-service ul li {
    margin-bottom: 0.6em;
  }
  
  .terms-of-service a {
    color: #2980b9;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.2s ease-in-out;
  }
  
  .terms-of-service a:hover {
    color: #1c6ea4;
  }
  
  /* Section divider styling */
  .section-divider {
    margin: 1.5em 0;
    border-top: 1px solid #e2e2e2;
  }
  
  /* Button styling (if needed for links) */
  .terms-of-service button, .terms-of-service a {
    background-color: #2980b9;
    color: #ffffff;
    padding: 10px 20px;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .terms-of-service button:hover, .terms-of-service a:hover {
    background-color: #353f45;
    color: #ffffff;
  }
  
  /* Footer styling for "Effective Date" */
  .terms-of-service p:last-child {
    font-size: 0.9em;
    font-style: italic;
    color: #7f8c8d;
    text-align: center;
    padding-top: 1.5em;
  }
  
  /* Responsive Design */
  
  /* Tablets */
  @media (max-width: 767px) {
    h1 {
      font-size: 2em;
    }
  
    h2 {
      font-size: 1.4em;
    }
  
    .terms-of-service {
      padding: 15px;
      margin: 15px;
    }
  
    .terms-of-service p,
    .terms-of-service ul {
      font-size: 0.95em;
    }
  }
  
  /* Mobile Devices */
  @media (max-width: 540px) {
    h1 {
      font-size: 1.8em;
    }
  
    h2 {
      font-size: 1.2em;
    }
  
    .terms-of-service {
      padding: 10px;
      margin: 10px;
      box-shadow: none;
    }
  
    .terms-of-service p,
    .terms-of-service ul {
      font-size: 0.9em;
    }
  }
  