/* General Header Styling */
.header {
    background-color: #333;
    height: 160px;
    margin-top: -30px;
    color: white;
    padding: 20px 60px; /* Adjust padding to balance layout */
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  .divtitle {
    position: relative; /* This allows absolute positioning of the <h1> */
    width: 120px; 
    height: 60px;      /* Set a fixed height (or 100% depending on your needs) */
    margin-top: -25px;       /* Set the container size */
    margin-right: 50px;       /* Set the container size */
    margin-left: 20px;       /* Set the container size */
    background-image: url('../../public/1322164.png');
    background-size: cover;   /* Ensures the image covers the entire container */
    background-position: center;  
    animation: rotateImage 10s infinite linear;
  }
  @keyframes rotateImage {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .h1title {
    margin-top: 130px;
    margin-left: -110px;
    margin-right: 50px;
    font-size: 15px;       /* Adjust the font size */
  }
  
