  /* Default Navigation Links Styling */
  .nav-links {
    display: flex;
    gap: 1rem;
  }
  
  .nav-links a {
    color: white;
    text-decoration: none;
  }
  
  .nav-links a:hover {
    text-decoration: underline;
  }
  
  /* Hamburger Menu Button Styling (hidden on large screens) */
  .menu-button {
    margin-left: 100px;
    visibility: visible;
    display: none;
    background: none;
    color: white;
    font-size: 30px;
    border: none;
    cursor: pointer;
    margin-right: -40px;
    transition: opacity 0.1s ease;
    z-index: 20; 
  }
 
  
  /* Mobile Styling */
  @media (max-width: 768px) {
    .nav-links {
      display: flex;
      flex-direction: column;
      position: fixed;
      top: 0;
      right: -100%; /* Start hidden outside of the screen */
      height: 100%;
      width: 250px;
      background-color: red;
      padding-top: 2rem;
      transition: right 0.3s ease; /* Slide-in effect */
      z-index: 15;
      
    }
  
    /* Show the menu when 'open' class is added */
    .nav-links.open {
      right: 50px;
      top: -50px;
      height: 400px;
      margin-top: 100px;
      background: #333;
      
    }
  
    /* Display the menu button on mobile */
    .menu-button {
      display: block; /* Default state - visible */
    }
    
    
    
