    /* Overlay Styling */
    .menu-overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 10; /* Ensure the overlay is below the nav links */
    }
  }
  
  /* Marquee container styling */
  .marquee-container {
    width: 100%;
    white-space: nowrap;
    height: 150px;
    background-color: transparent; /* Set the background color */
    overflow: hidden; /* Hide the content that's outside the container */
    position: relative;
    border-radius: 10px;
    margin-top: 29px; /* Optional: adds space below the marquee */
  }
  .scrolling-images {
    display: inline-flex;
    animation: scroll 15s linear infinite;
  }
  /* Styling for the image inside the marquee */
  .marquee-container .scrolling-images {
    display: flex; /* Use flex to line up images horizontally */
    animation: scrollImage 15s linear infinite;

  }
  
  .marquee-container .scrolling-images img {
    width: 100%; /* Ensure each image takes full container width */
    height: 100%; /* Adjust to desired height */
    object-fit: cover; /* Ensure images fit within the container without distortion */
  }
  
  /* Keyframes for scrolling the images */
  @keyframes scrollImage {
    0% {
      transform: translateX(0); /* Start from the first image */
    }
    100% {
      transform: translateX(-100%); /* Move the images to the left */
    }
  }
  @media (max-width:540px) {
    .marquee-container {
      width: 100%;
      margin-right: -100px;
      margin-left: -30px;
    }
    .divtitle{
      margin-left: -30px;
      width: 50px; 
    height: 50px; 
