/* Base styling for a clean, professional look */
body {
    background-color: #f9f9f9;
    color: #333;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .h1, .h2 {
    color: #1a1a1a;
    margin: 0;
    padding: 0.5em 0;
    font-weight: 600;
    text-align: left;
  }
  
  .h1 {
    font-size: 2.5em;
    text-align: center;
    color: #0056b3;
  }
  
  .h2 {
    font-size: 1.5em;
    margin-top: 1em;
    color: #333;
  }
  
  .privacy-policy {
    max-width: 900px;
    margin: 20px auto;
    padding: 20px 30px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    line-height: 1.6;
    color: #4a4a4a;
  }
  
  .privacy-policy p {
    font-size: 1em;
    margin: 1em 0;
  }
  
  .privacy-policy ul {
    padding-left: 1.5em;
    list-style: disc;
  }
  
  .privacy-policy ul li {
    margin-bottom: 0.5em;
  }
  
  .privacy-policy a {
    color: #0056b3;
    text-decoration: none;
    transition: color 0.2s ease-in-out;
  }
  
  .privacy-policy a:hover {
    color: #004494;
  }
  
  /* Button styling for links (if needed) */
  .privacy-policy button, .privacy-policy a {
    background-color: #0056b3;
    color: #fff;
    padding: 8px 15px;
    border-radius: 4px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .privacy-policy button:hover, .privacy-policy a:hover {
    background-color: #004494;
  }
  
  /* Footer styling for the "Last Updated" section */
  .privacy-policy p:last-child {
    font-size: 0.9em;
    font-style: italic;
    color: #666;
    text-align: center;
    padding-top: 1em;
  }
  
  /* Responsive Design */
  
  /* Tablets */
  @media (max-width: 767px) {
    h1 {
      font-size: 2em;
    }
  
    h2 {
      font-size: 1.2em;
    }
  
    .privacy-policy {
      padding: 15px;
      margin: 15px;
    }
  
    .privacy-policy p,
    .privacy-policy ul {
      font-size: 0.95em;
    }
  }
  
  /* Mobile Devices */
  @media (max-width: 540px) {
    h1 {
      font-size: 1.8em;
    }
  
    h2 {
      font-size: 1.1em;
    }
  
    .privacy-policy {
      padding: 10px;
      margin: 10px;
      box-shadow: none;
    }
  
    .privacy-policy p,
    .privacy-policy ul {
      font-size: 0.9em;
    }
  }
  