/* General footer styles */
.footer {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
}

.footer-links a {
  color: white;
  margin: 0 0.5rem;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.footer-links-left,
.footer-links-right {
  flex: 1;
  text-align: left;
  margin-left: 100px;
}

.footer-links-right {
  margin-right: 60px;
}

.ppp {
  margin-right: 60px;
  margin-top: -5px;
  margin-bottom: -5px;
}

.ppp:hover {
  color: rgba(255, 255, 255, 0.527);
}

.footer-links-left a,
.footer-links-right a {
  color: white;
  margin: 0 0.5rem;
  text-decoration: none;
}

.footer-links-left a:hover,
.footer-links-right a:hover {
  text-decoration: underline;
}

.footer-links-main {
  flex: 2;
  text-align: center;
}

.footer-links a {
  color: #ffffff;
  text-decoration: none;
  padding: 0 10px;
}

.footer-links a:hover {
  text-decoration: underline;
}

.footer-icons a {
  color: #333;
  text-decoration: none;
}

.footer-icons a:hover {
  color: #0056b3;
}

/* Responsive Design for smaller screens */
@media (max-width: 767px) {
  .footer-content {
    flex-direction: column; /* Stack footer elements vertically */
    text-align: center; /* Center content */
  }

  .footer-links-left,
  .footer-links-right {
    margin-left: 0; /* Remove left margin for smaller screens */
    margin-right: 0; /* Remove right margin for smaller screens */
    text-align: center; /* Center align the links */
    margin-bottom: 1rem; /* Add spacing between sections */
  }

  .footer-links-main {
    margin-bottom: 1rem; /* Add spacing between sections */
  }

  .ppp {
    margin-right: 0; /* Remove the margin-right */
  }
}

/* For very small screens (mobile) with max-width: 540px */
@media (max-width: 540px) {
  .footer-content {
    padding: 1rem; /* Add padding for smaller screens */
  }
  
  .footer-links-left,
  .footer-links-right,
  .footer-links-main {
    width: 100%; /* Allow the links to take up full width */
    margin-left: 0;
    margin-right: 0;
  }

  .footer-links-left a,
  .footer-links-right a {
    margin: 0.5rem 0; /* Adjust vertical spacing for links */
  }

  .footer-icons {
    margin-top: 1rem; /* Add some spacing above the footer icons */
    text-align: center; /* Center icons */
  }

  .footer-icons a {
    margin: 0 10px; /* Add spacing between icons */
  }
}
